import React, {Component} from 'react';

import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import EmailIcon from '@material-ui/icons/Email';

export default class Contact extends Component {
  state = {
    name: '',
    email: '',
    subject: '',
    description: ''
  };

  render() {
    const handleChange = name => event => {
      this.setState({ [name]: event.target.value });
      // setValues({ ...values, [name]: event.target.value });
    };

    return (
      <div style={{padding: '150px 0 100px'}}>
        <div style={{display: 'inline-block', width: '50%'}}>
          <Typography variant='h2'>Contact Me</Typography>
          <div style={{marginTop: 10}}>
            <EmailIcon />
            <Typography variant='subtitle2' style={{display: 'inline-block', verticalAlign: 'super', marginLeft: 10}}>jb.bae@outlook.com</Typography>
          </div>
        </div>
        <div style={{display: 'inline-block', width: '50%', verticalAlign: 'top', textAlign: 'left'}}>
          <div style={{width: '80%'}}>
            <TextField
              id="outlined-name"
              label="Name"
              style={{display: 'block'}}
              value={this.state.name}
              onChange={handleChange('name')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id="outlined-email"
              label="Email"
              style={{display: 'block'}}
              value={this.state.email}
              onChange={handleChange('email')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id="outlined-subject"
              label="Subject"
              style={{display: 'block'}}
              value={this.state.subject}
              onChange={handleChange('subject')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id="outlined-description"
              label="Description"
              style={{display: 'block'}}
              fullWidth
              multiline
              rows='4'
              value={this.state.description}
              onChange={handleChange('description')}
              margin="normal"
              variant="outlined"
            />
            <Button variant='contained' color='secondary' style={{marginTop: 20}}>Send</Button>
          </div>
        </div>
      </div>
    );
  }
}
