import React, { Component } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
// import { pdfjs } from 'react-pdf';

import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
// import Typography from '@material-ui/core/Typography';

import resumePDF from '../assets/Jae Beom Bae Resume.pdf';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

export default class Resume extends Component {

  onDocumentLoadSuccess = ({ numPages }) => {
    // this.setState({ numPages });
  }

  render() {
    return (
      <div style={{padding: '100px 0'}}>
        <div style={{marginBottom: 50}}>
          {/* <Typography variant='h3'>Resume</Typography> */}
          <Button href={resumePDF} target='_blank' rel="noopener noreferrer" color='primary' variant='contained'>Download</Button>
        </div>
        <div style={{width: 800, margin: 'auto'}}>
          <Document
            file={resumePDF}
            onLoadSuccess={this.onDocumentLoadSuccess}
            loading={<CircularProgress style={{margin: '100px auto'}} size={150} />}
          >
            <Page pageNumber={1} width={800} />
          </Document>
        </div>
      </div>
    );
  }
}
