import React, { Component } from 'react';
import { Link } from "react-router-dom";

import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';

import ContentfulCli from '../services/ContentfulCli';
import ProjBlock from './ProjBlock';

export default class Home extends Component {
  state = {
    projects_DS: [],
    projects_W: []
  };

  componentWillMount() {
    ContentfulCli.getEntries({ 'content_type': 'dsProject' }).then((entries) => {
      if (entries.items) {
        this.setState({
          projects_DS: entries.items
        });
      } else {
        console.log('Content not Found!');
      }
    });

    ContentfulCli.getEntries({ 'content_type': 'webProject' }).then((entries) => {
      if (entries.items) {
        this.setState({
          projects_W: entries.items
        });
      } else {
        console.log('Content not Found!');
      }
    });
  }

  render() {
    let style = {
      sectionHeadDiv: {
        padding: '70px 0',
        backgroundColor: '#212121'
      }
    };

    let ds_blocks = [];
    let web_blocks = [];

    for (var i = 0; i < this.state.projects_DS.length; i++) {
      let projObj_DS = this.state.projects_DS[i].fields;
      console.log(projObj_DS);

      ds_blocks.push(
        <ProjBlock
          id={projObj_DS.id}
          title={projObj_DS.title}
          subtitle={projObj_DS.subtitle}
          chars={projObj_DS.chars}
          ongoing={projObj_DS.ongoing}
          imgURL={projObj_DS.coverImg.fields.file.url}
          links={projObj_DS.links} />
      );
    }

    for (var j = 0; j < this.state.projects_W.length; j++) {
      let projObj_W = this.state.projects_W[j].fields;

      web_blocks.push(<ProjBlock id={projObj_W.id} title={projObj_W.title} subtitle={projObj_W.subtitle} imgURL={projObj_W.coverImg.fields.file.url} links={projObj_W.links} />);
    }

    return (
      <div>
        <div style={{padding: '250px 0'}}>
          <Typography variant='h4' style={{width: '80%', margin: 'auto', lineHeight: 1.7}}>
            {/* <span style={{fontWeight: 500, color: '#1e88e5'}}>Data Scientist</span>, <span style={{fontWeight: 500, color: '#e91e63'}}>Product Manager</span>, and <span style={{fontWeight: 500, color: '#4caf50'}}>Full-Stack Developer</span> passionate about tackling resource productivity */}
            <span style={{fontWeight: 500, color: '#1e88e5'}}>Data Scientist</span>, <span style={{fontWeight: 500, color: '#e91e63'}}>Product Manager</span>, and <span style={{fontWeight: 500, color: '#4caf50'}}>Full-Stack Developer</span> who<br/>💚s merging tech + business to build data-driven solutions for
            <br/>
            the <Tooltip title="Which ones?" aria-label="more"><Link to='/about' style={{color: "#212121"}}>world's resource challenges</Link></Tooltip>.
          </Typography>
          {/* <Typography variant='subtitle1'>(Energy, Water, Food, Human Capital)</Typography> */}
        </div>
        <div>
          <div style={style.sectionHeadDiv}>
            <Typography variant='h4' style={{color: 'white'}}><span role='img' aria-label='chart'>📊</span> Data Science</Typography>
          </div>
          <div style={{padding: '80px 50px'}}>
            {ds_blocks}
          </div>
        </div>
        <div>
          <div style={style.sectionHeadDiv}>
            <Typography variant='h4' style={{color: 'white'}}><span role='img' aria-label='web'>🌍</span> Web</Typography>
          </div>
          <div style={{padding: '80px 0'}}>
            {web_blocks}
          </div>
        </div>
      </div>
    );
  }
}
