import React, {Component} from 'react';
import { Link as RouterLink } from 'react-router-dom';

import Button from '@material-ui/core/Button';
import Chip from '@material-ui/core/Chip';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';

const PageLink = React.forwardRef((props, ref) => <RouterLink innerRef={ref} {...props} />);

export default class ProjBlock extends Component {
  state = {
    // projRaise: false
  };

  render() {
    let styles = {
      mediaStyle: {
        height: 250,
        backgroundSize: this.props.id === 'customer_segments_germany' ? "contain" : null
      },
      mediaStyle_P: {
        height: 209,
        backgroundSize: this.props.id === 'customer_segments_germany' ? "contain" : null
      }
    }

    let actionButts = [];

    if (this.props.links) {
      if (this.props.links.page) {
        actionButts.push(<Button size="small" color="secondary" component={PageLink} to={'/projects' + this.props.links.page}>About</Button>);
      }

      if (this.props.links.app) {
        actionButts.push(<Button size="small" color="primary" href={this.props.links.app} target='_blank' rel="noopener noreferrer">App</Button>);
      }

      if (this.props.links.github) {
        actionButts.push(<Button size="small" color="primary" href={this.props.links.github} target='_blank' rel="noopener noreferrer">GitHub</Button>);
      }

      if (this.props.links.medium) {
        actionButts.push(<Button size="small" color="primary" href={this.props.links.medium} target='_blank' rel="noopener noreferrer">Medium</Button>);
      }

      if (this.props.links.slides) {
        actionButts.push(<Button size="small" color="primary" href={this.props.links.slides} target='_blank' rel="noopener noreferrer">Slides</Button>);
      }
    }

    let charChips = [];

    if (this.props.chars) {
      for (var i = 0; i < this.props.chars.length; i++) {
        charChips.push(<Chip label={this.props.chars[i]} color='primary' style={{margin: 5}} />);
      }
    }

    // raised={this.state.projRaise}
    // onMouseEnter={() => { this.setState({ projRaise: true }) }}
    // onMouseLeave={() => { this.setState({ projRaise: false }) }}>]

    return (
      <div style={{display: 'inline-block', margin: 'auto', verticalAlign: 'top'}}>
        <Card
          key={this.props.id}
          style={{width: 350, margin: 20}}
          raised>
          <CardMedia
            style={this.props.title === 'Pivot' ? styles.mediaStyle_P :  styles.mediaStyle}
            image={"https:" + this.props.imgURL}
            title={this.props.title}
          />
          <CardContent style={{minHeight: 115, textAlign: 'left'}}>
            <Typography gutterBottom variant="h5" component="h2" style={{minHeight: 62}}>
              {this.props.title}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              {this.props.subtitle}
            </Typography>
            <div style={{marginTop: 10}}>
              {charChips}
            </div>
          </CardContent>
          <CardActions>
            {actionButts}
          </CardActions>
        </Card>
      </div>
    )
  }
}
