import React, { Component } from 'react';
import { BLOCKS } from '@contentful/rich-text-types';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import ContentfulCli from '../services/ContentfulCli';

export default class ProjectPage extends Component {
  state = {
    contentObj: {}
  };

  componentWillMount() {
    console.log(this.props.match.params.projectId);
    ContentfulCli.getEntries({ 'fields.id': this.props.match.params.projectId, 'content_type': 'webProject' }).then((entries) => {
      if (entries.items[0]) {
        this.setState({
          contentObj: entries.items[0].fields
        });
        console.log(entries.items[0].fields);
      } else {
        console.log('Content not Found!');
      }
    });
  }

  render() {
    let styles = {
      root: {
        fontFamily: 'Roboto',
        lineHeight: 1.75,
        fontSize: '1.125rem',
        color: '#283039'
      },
      sectionHead: {
        color: 'white'
      },
      sectionHeadDiv: {
        padding: '50px 0',
        backgroundColor: '#1588CE'
      },
      textBlock: {
        textAlign: 'left',
        padding: '100px 0',
        width: '70%',
        margin: 'auto'
      },
      textBlock_Split: {
        verticalAlign: 'top',
        display: 'inline-block',
        textAlign: 'left',
        width: '50%',
        margin: 'auto'
      },
      imgBlockB_Split : {
        display: 'inline-block',
        width: '60%'
      },
      textBlockB_Split: {
        verticalAlign: 'top',
        display: 'inline-block',
        textAlign: 'left',
        width: '40%',
        margin: 'auto'
      },
      img_Split: {
        width: '80%',
        margin: 'auto'
      },
      halfDiv: {
        width: '60%',
        margin: 'auto'
      }
    };

    const options = {
      renderNode: {
        [BLOCKS.EMBEDDED_ASSET]: (node) => {
          const imgURL = node.data.target.fields.file.url;
          return <img src={imgURL} style={{width: '75%', margin: 'auto', display: 'block'}} />
        }
      }
    };


    return (
      <div style={styles.root}>
        <div style={{padding: '200px 0'}}>
          <Typography variant='h2'>{this.state.contentObj.title}</Typography>
          <Typography variant='h6' style={{margin: '25px auto'}}>{this.state.contentObj.subtitle}</Typography>
          {this.state.contentObj.links ? <Button variant='contained' color='secondary' href={this.state.contentObj.links.app} target='_blank' rel="noopener noreferrer">See App</Button> : null}
        </div>

        <div style={styles.sectionHeadDiv}>
          <Typography variant='h4' style={styles.sectionHead}>Overview</Typography>
        </div>
        <div style={{padding: '50px 0'}}>
          <div style={styles.textBlock_Split}>
            <div style={styles.halfDiv}>
              {documentToReactComponents(this.state.contentObj.stats)}
            </div>
          </div>
          <div style={styles.textBlock_Split}>
            <div style={{width: '60%', margin: 'auto'}}>
              {documentToReactComponents(this.state.contentObj.overview)}
            </div>
          </div>
        </div>

        <div>
          {this.state.contentObj.ssMain ? <img style={{width: '100%', marginBottom: -10}} src={this.state.contentObj.ssMain.fields.file.url} /> : null}
        </div>

        {/* <div style={styles.sectionHeadDiv}>
          <Typography variant='h4' style={styles.sectionHead}>Process</Typography>
        </div>
        <div style={styles.textBlock}>
          {documentToReactComponents(this.state.contentObj.process, options)}
        </div> */}

        <div style={styles.sectionHeadDiv}>
          <Typography variant='h4' style={styles.sectionHead}>Problem</Typography>
        </div>
        <div style={styles.textBlock}>
          <div>
            {documentToReactComponents(this.state.contentObj.problemOver)}
          </div>
          <div>
            <Typography variant='h6'>Persona</Typography>
          </div>
          <div>
            {documentToReactComponents(this.state.contentObj.problemPersonas)}
          </div>
          <div>
            <Typography variant='h6'>Validation</Typography>
          </div>
          <div>
            {documentToReactComponents(this.state.contentObj.problemValidate)}
          </div>
          <div>
            {this.state.contentObj.problemValidateImg ? <img style={{width: '100%'}} src={this.state.contentObj.problemValidateImg.fields.file.url} /> : null}
          </div>
        </div>

        <div style={styles.sectionHeadDiv}>
          <Typography variant='h4' style={styles.sectionHead}>Build</Typography>
        </div>
        <div style={styles.textBlock}>
          <div>
            {documentToReactComponents(this.state.contentObj.buildOver)}
          </div>
          <div>
            <Typography variant='h6'>Design</Typography>
          </div>
          <div>
            {documentToReactComponents(this.state.contentObj.buildDesign)}
          </div>
          <div>
            {this.state.contentObj.buildDesignImg ? <img style={styles.img_Split} src={this.state.contentObj.buildDesignImg.fields.file.url} /> : null}
          </div>
          <div>
            <Typography variant='h6'>Implement</Typography>
          </div>
          <div>
            <div style={styles.textBlockB_Split}>
              {documentToReactComponents(this.state.contentObj.buildImplement)}
            </div>
            <div style={styles.textBlockB_Split}>
              {this.state.contentObj.buildImplementImg ? <img style={styles.img_Split} src={this.state.contentObj.buildImplementImg.fields.file.url} /> : null}
            </div>
          </div>
          <div>
            <Typography variant='h6'>Iterate</Typography>
          </div>
          <div>
            {documentToReactComponents(this.state.contentObj.buildIterate)}
          </div>
        </div>

        <div style={styles.sectionHeadDiv}>
          <Typography variant='h4' style={styles.sectionHead}>Launch</Typography>
        </div>
        <div style={styles.textBlock}>
          {documentToReactComponents(this.state.contentObj.launch)}
        </div>
        <div style={styles.textBlock}>
            {this.state.contentObj.launchImg ? <img style={styles.img_Split} src={this.state.contentObj.launchImg.fields.file.url} /> : null}
        </div>

        <div style={styles.sectionHeadDiv}>
          <Typography variant='h4' style={styles.sectionHead}>Closing Thoughts</Typography>
        </div>
        <div style={styles.textBlock}>
          {documentToReactComponents(this.state.contentObj.conclusion)}
        </div>
      </div>
    );
  }
}
