import React from 'react';
import { BrowserRouter as Router, Switch, Route, NavLink, Link } from "react-router-dom";

// import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import About from './components/About';
import Contact from './components/Contact';
import Home from './components/Home';
import Resume from './components/Resume';
import ProjectPage from './components/ProjectPage';

import './App.css';

export default function App() {
  return (
    <Router>
      <div className="App">
        <nav style={{position: 'absolute', width: '100%'}}>
          <div style={{display: 'flex', justifyContent: 'space-between', padding: 30}}>
            <div style={{display: 'inline-block', order: 1}}>
              <Link exact to='/' style={{textDecoration: 'none', color: '#212121'}}><Typography variant='h5'>Jae Beom Bae</Typography></Link>
            </div>
            <div className="navbar-list" style={{display: 'inline-block', order: 2}}>
              <NavLink exact to='/'>Work</NavLink>
              <NavLink to='/resume'>Resume</NavLink>
              <NavLink to='/about'>About</NavLink>
              <NavLink to='/contact'>Contact</NavLink>
            </div>
          </div>
        </nav>
        <Switch>
          <Route path="/resume">
            <Resume />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route path={"/projects/:projectId"} component={ProjectPage} />
          <Route path="/">
            <Home />
          </Route>
        </Switch>
        <div style={{ padding: '100px 0', backgroundColor: '#212121' }}>
          <Typography variant='subtitle1' style={{color: 'white'}}>Built with 💛 using <a className='bottomLink' href='https://reactjs.org' target='_blank' rel="noopener noreferrer">React</a>, <a className='bottomLink' href='https://material-ui.com' target='_blank' rel="noopener noreferrer">Material-UI</a>, and <a className='bottomLink' href='https://contentful.com' target='_blank' rel="noopener noreferrer">Contentful</a></Typography>
          <Typography variant='subtitle2' style={{color: 'white'}}>© Jae Beom Bae 2019</Typography>
        </div>
      </div>
    </Router>
  );
}
